import React, { useEffect, useReducer, useState } from "react";
// import { useNavigate } from "react-router-dom";
// import { toast } from "react-toastify";
import axios from "axios";
// import { Store } from "../Store";
// import { getError } from "../utils";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import { Helmet } from "react-helmet-async";
import LoadingBox from "../components/LoadingBox";
import MessageBox from "../components/MessageBox";
import Button from "react-bootstrap/Button";

const reducer = (state, action) => {
  switch (action.type) {
    case "FETCH_REQUEST":
      return { ...state, loading: true };
    case "FETCH_SUCCESS":
      return { ...state, loading: false };
    case "FETCH_FAIL":
      return { ...state, loading: false, error: action.payload };
    case "UPDATE_REQUEST":
      return { ...state, loadingUpdate: true };
    case "UPDATE_SUCCESS":
      return { ...state, loadingUpdate: false };
    case "UPDATE_FAIL":
      return { ...state, loadingUpdate: false };
    case "UPLOAD_REQUEST":
      return { ...state, loadingUpload: true, errorUpload: "" };
    case "UPLOAD_SUCCESS":
      return {
        ...state,
        loadingUpload: false,
        errorUpload: "",
      };
    case "UPLOAD_FAIL":
      return { ...state, loadingUpload: false, errorUpload: action.payload };

    default:
      return state;
  }
};
export default function CreateOrderScreen() {
  const [{ loading, error, loadingUpdate }, dispatch] = useReducer(reducer, {
    loading: true,
    error: "",
  });

  const [name, setName] = useState("");
  const [quantity, setQuantity] = useState("");
  const [selectedBranch, setSelectedBranch] = useState("");
  const [branchs, setBranchs] = useState([]);

  const [services, setServices] = useState([]);
  const [selectedService, setSelectedService] = useState("");
  const [selectedServiceAmount, setSelectedServiceAmount] = useState(0);

  const [products, setProducts] = useState([]);
  const [selectedProduct, setSelectedProduct] = useState("");

  const [shipping, setShipping] = useState("");

  const fetchData = async () => {
    try {
      const branchResponse = await axios.get(
        "https://api.sabil.ly/v1/packages/b2n/?sort=ASC"
      );
      setBranchs(branchResponse.data.data);
      const servicesResponse = await axios.get(
        "https://api.sabil.ly/v1/packages/services/?sort=ASC"
      );
      setServices(servicesResponse.data.data);
      const productsResponse = await axios.get(
        "https://amf.hajat.com.ly/store/products"
      );
      setProducts(productsResponse.data.products);
      dispatch({
        type: "FETCH_SUCCESS",
      });
    } catch (error) {
      console.error("There was a problem with the fetch operation:", error);
    }
  };
  const checkShippingPrice = async () => {
    if (selectedBranch === "") {
      return;
    }
    let data = JSON.stringify({
      from: {
        city: "زناتة 10",
        country: "Libya",
        address: "",
      },
      to: {
        city: selectedBranch,
        country: "Libya",
        address: "",
      },
    });

    let config = {
      method: "post",
      maxBodyLength: Infinity,
      url: "https://api.sabil.ly/v1/pricings/shipping",
      headers: {
        "Content-Type": "application/json",
      },
      data: data,
    };

    axios
      .request(config)
      .then((response) => {
        setShipping(
          response.data.data.shipping.branchToBranch +
            response.data.data.shipping.fromBranch +
            response.data.data.shipping.toBranch +
            selectedServiceAmount
        );
      })
      .catch((error) => {
        console.log(error);
      });
  };

  useEffect(() => {
    fetchData();
  }, []);

  return (
    <Container className="small-container">
      <Helmet>
        <title>Create Product</title>
      </Helmet>
      <h1>Create Product</h1>

      {loading ? (
        <LoadingBox></LoadingBox>
      ) : error ? (
        <MessageBox variant="danger">{error}</MessageBox>
      ) : (
        <Form>
          <h1>
            {shipping || ""} {selectedBranch.replace(/\d+/g, "") || ""}
          </h1>
          <Form.Group className="mb-3" controlId="name">
            <Form.Label>Name</Form.Label>
            <Form.Control
              value={name}
              onChange={(e) => setName(e.target.value)}
              required
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="phoneNumber">
            <Form.Label>Phone Number</Form.Label>
            <Form.Control
              value={name}
              onChange={(e) => setName(e.target.value)}
              required
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="branch">
            <Form.Label>Branch</Form.Label>
            <Form.Select
              onChange={(e) => {
                setSelectedBranch(e.target.value);
                checkShippingPrice();
              }}
              value={selectedBranch}
            >
              <option>Select a branch</option>
              {branchs.map((branch) => (
                <option key={branch.b2nPackageId} value={branch.node}>
                  {branch.node.replace(/\d+/g, "")}
                </option>
              ))}
            </Form.Select>
          </Form.Group>
          <Form.Group className="mb-3" controlId="service">
            <Form.Label>Services</Form.Label>
            <Form.Select
              onChange={(e) => {
                setSelectedService(e.target.value);
                setSelectedServiceAmount(
                  parseInt(e.target.value.split("@")[1])
                );
                checkShippingPrice();
              }}
              value={selectedService}
            >
              <option>Select a service</option>
              {services.map((service) =>
                service.isPublic && service.allowedUserIds[0] !== "dma" ? (
                  <option
                    key={service.servicePackageId}
                    value={`${service.servicePackageId}@${service.amount}`}
                  >
                    {service.title + " " + service.amount}
                  </option>
                ) : (
                  <br></br>
                )
              )}
            </Form.Select>
          </Form.Group>
          <Form.Group className="mb-3" controlId="products">
            <Form.Label>Services</Form.Label>
            <Form.Select
              onChange={(e) => setSelectedProduct(e.target.value)}
              value={selectedProduct}
            >
              <option>Select a product</option>
              {products.map((product) => (
                <option key={product.id} value={product.id}>
                  {product.title}
                </option>
              ))}
            </Form.Select>
          </Form.Group>

          <Form.Group className="mb-3" controlId="quantity">
            <Form.Label>Quantity</Form.Label>
            <Form.Control
              value={quantity}
              onChange={(e) => setQuantity(e.target.value)}
              required
            />
          </Form.Group>

          <center>
            <div className="mb-3">
              <Button disabled={loadingUpdate} type="submit">
                Create
              </Button>
              {loadingUpdate && <LoadingBox></LoadingBox>}
            </div>
          </center>
          {/* <Form.Group className="mb-3" controlId="imageFile">
            <Form.Label>Upload Image</Form.Label>
            <Form.Control type="file" onChange={uploadFileHandler} />
            {loadingUpload && <LoadingBox></LoadingBox>}
          </Form.Group> */}
        </Form>
      )}
    </Container>
  );
}
