import { BrowserRouter, Link, Route, Routes } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import HomeScreen from "./screens/HomeScreen";
import Navbar from "react-bootstrap/Navbar";
import Badge from "react-bootstrap/Badge";
import Nav from "react-bootstrap/Nav";
import NavDropdown from "react-bootstrap/NavDropdown";
import Container from "react-bootstrap/Container";
import { LinkContainer } from "react-router-bootstrap";
import { useContext } from "react";
import { Store } from "./Store";
import CartScreen from "./screens/CartScreen";
import SigninScreen from "./screens/SigninScreen";
import ShippingAddressScreen from "./screens/ShippingAddressScreen";
import SignupScreen from "./screens/SignupScreen";
import PaymentMethodScreen from "./screens/PaymentMethodScreen";
import PlaceOrderScreen from "./screens/PlaceOrderScreen";
import OrderScreen from "./screens/OrderScreen";
import OrderHistoryScreen from "./screens/OrderHistoryScreen";
import ProfileScreen from "./screens/ProfileScreen";
// import Button from "react-bootstrap/Button";
import SearchBox from "./components/SearchBox";
import SearchScreen from "./screens/SearchScreen";
import ProtectedRoute from "./components/ProtectedRoute";
import DashboardScreen from "./screens/DashboardScreen";
import AdminRoute from "./components/AdminRoute";
import ProductListScreen from "./screens/ProductListScreen";
import ProductEditScreen from "./screens/ProductEditScreen";
import OrderListScreen from "./screens/OrderListScreen";
import UserListScreen from "./screens/UserListScreen";
import UserEditScreen from "./screens/UserEditScreen";
import MapScreen from "./screens/MapScreen";
import ForgetPasswordScreen from "./screens/ForgetPasswordScreen";
import ResetPasswordScreen from "./screens/ResetPasswordScreen";
import RestaurantScreen from "./screens/RestaurantScreen";
import MakeOrderScreen from "./screens/MakeOrderScreen";
import RestaurantListScreen from "./screens/RestaurantListScreen";
import RestaurantEditScreen from "./screens/RestaurantEditScreen";
import ProductCreateScreen from "./screens/ProductCreateScreen";
import RestaurantCreateScreen from "./screens/RestaurantCreateScreen";
import DriverCreateScreen from "./screens/DriverCreateScreen";
import DriverEditScreen from "./screens/DriverEditScreen";
import axios from "axios";
import SearchBox2 from "./components/SearchBox2";
import SearchScreen2 from "./screens/SearchScreen2";
import CreateOrderScreen from "./screens/CreateOrderScreen";
// axios.defaults.baseURL = 'http://localhost:4000';
axios.defaults.baseURL = "https://ilygogo-110ca5d4def3.herokuapp.com";
function App() {
  const { state, dispatch: ctxDispatch } = useContext(Store);
  const { fullBox, cart, userInfo } = state;

  const signoutHandler = () => {
    ctxDispatch({ type: "USER_SIGNOUT" });
    localStorage.removeItem("userInfo");
    localStorage.removeItem("shippingAddress");
    localStorage.removeItem("paymentMethod");
    window.location.href = "/menus/signin";
  };

  return (
    <BrowserRouter>
      <div
        className={
          fullBox
            ? "site-container d-flex flex-column full-box"
            : "site-container d-flex flex-column"
        }
      >
        <ToastContainer position="bottom-center" limit={1} />
        <header>
          <Navbar bg="dark" variant="dark" expand="lg">
            <Container>
              <LinkContainer to="/menus">
                <Navbar.Brand>Talabaty</Navbar.Brand>
              </LinkContainer>
              <Navbar.Toggle aria-controls="basic-navbar-nav" />
              <Navbar.Collapse id="basic-navbar-nav">
                <SearchBox />
                <SearchBox2 />
                <Nav className="me-auto  w-100  justify-content-end">
                  <Link to="/menus/cart" className="nav-link">
                    Cart
                    {cart.cartItems.length > 0 && (
                      <Badge pill bg="danger">
                        {cart.cartItems.reduce((a, c) => a + c.quantity, 0)}
                      </Badge>
                    )}
                  </Link>
                  {userInfo ? (
                    <NavDropdown title={userInfo.name} id="basic-nav-dropdown">
                      <LinkContainer to="/menus/admin/dashboard">
                        <NavDropdown.Item>Dashboard</NavDropdown.Item>
                      </LinkContainer>
                      <LinkContainer to="/menus/profile">
                        <NavDropdown.Item>User Profile</NavDropdown.Item>
                      </LinkContainer>
                      <LinkContainer to="/menus/orderhistory">
                        <NavDropdown.Item>Order History</NavDropdown.Item>
                      </LinkContainer>
                      <NavDropdown.Divider />
                      <Link
                        className="dropdown-item"
                        to="#signout"
                        onClick={signoutHandler}
                      >
                        Sign Out
                      </Link>
                    </NavDropdown>
                  ) : (
                    <Link className="nav-link" to="/menus/signin">
                      Sign In
                    </Link>
                  )}
                  {userInfo && userInfo.isAdmin && (
                    <NavDropdown title="Admin" id="admin-nav-dropdown">
                      <LinkContainer to="/menus/admin/products">
                        <NavDropdown.Item>Products</NavDropdown.Item>
                      </LinkContainer>
                      <LinkContainer to="/menus/admin/orders">
                        <NavDropdown.Item>Orders</NavDropdown.Item>
                      </LinkContainer>
                      <LinkContainer to="/menus/admin/users">
                        <NavDropdown.Item>Users</NavDropdown.Item>
                      </LinkContainer>
                      <LinkContainer to="/menus/admin/restaurants">
                        <NavDropdown.Item>Restaurants</NavDropdown.Item>
                      </LinkContainer>
                    </NavDropdown>
                  )}
                </Nav>
              </Navbar.Collapse>
            </Container>
          </Navbar>
        </header>
        <main>
          <Container className="mt-3">
            <Routes>
              <Route
                path="/menus/restaurant/:id"
                element={<RestaurantScreen />}
              />
              <Route path="/menus/cart" element={<CartScreen />} />
              <Route path="/menus/search" element={<SearchScreen />} />
              <Route path="/menus/search2" element={<SearchScreen2 />} />
              <Route path="/menus/signin" element={<SigninScreen />} />
              <Route path="/menus/signup" element={<SignupScreen />} />
              <Route
                path="/menus/forget-password"
                element={<ForgetPasswordScreen />}
              />
              <Route
                path="/menus/reset-password/:token"
                element={<ResetPasswordScreen />}
              />

              <Route
                path="/menus/profile"
                element={
                  <ProtectedRoute>
                    <ProfileScreen />
                  </ProtectedRoute>
                }
              />
              <Route
                path="/menus/makeoreder"
                element={
                  <ProtectedRoute>
                    <MakeOrderScreen />
                  </ProtectedRoute>
                }
              />
              <Route
                path="/menus/map"
                element={
                  <ProtectedRoute>
                    <MapScreen />
                  </ProtectedRoute>
                }
              />
              <Route path="/menus/placeorder" element={<PlaceOrderScreen />} />
              <Route
                path="/menus/order/:id"
                element={
                  <ProtectedRoute>
                    <OrderScreen />
                  </ProtectedRoute>
                }
              ></Route>
              <Route
                path="/menus/orderhistory"
                element={
                  <ProtectedRoute>
                    <OrderHistoryScreen />
                  </ProtectedRoute>
                }
              ></Route>
              <Route
                path="/menus/shipping"
                element={<ShippingAddressScreen />}
              ></Route>
              <Route
                path="/menus/payment"
                element={<PaymentMethodScreen />}
              ></Route>
              {/* Admin Routes */}
              <Route
                path="/menus/admin/dashboard"
                element={<DashboardScreen />}
              ></Route>
              <Route
                path="/menus/create/product"
                element={
                  <AdminRoute>
                    <ProductCreateScreen />
                  </AdminRoute>
                }
              ></Route>
              <Route
                path="/menus/createorder"
                element={<CreateOrderScreen />}
              ></Route>
              <Route
                path="/menus/create/driver"
                element={
                  <AdminRoute>
                    <DriverCreateScreen />
                  </AdminRoute>
                }
              ></Route>
              <Route
                path="/menus/create/restaurant"
                element={
                  <AdminRoute>
                    <RestaurantCreateScreen />
                  </AdminRoute>
                }
              ></Route>
              <Route
                path="/menus/admin/orders"
                element={
                  <AdminRoute>
                    <OrderListScreen />
                  </AdminRoute>
                }
              ></Route>
              <Route
                path="/menus/admin/restaurants"
                element={
                  <AdminRoute>
                    <RestaurantListScreen />
                  </AdminRoute>
                }
              ></Route>
              <Route
                path="/menus/admin/users"
                element={
                  <AdminRoute>
                    <UserListScreen />
                  </AdminRoute>
                }
              ></Route>
              <Route
                path="/menus/admin/products"
                element={
                  <AdminRoute>
                    <ProductListScreen />
                  </AdminRoute>
                }
              ></Route>
              <Route
                path="/menus/admin/product/:id"
                element={
                  <AdminRoute>
                    <ProductEditScreen />
                  </AdminRoute>
                }
              ></Route>
              <Route
                path="/menus/admin/restaurant/:id"
                element={
                  <AdminRoute>
                    <RestaurantEditScreen />
                  </AdminRoute>
                }
              ></Route>
              <Route
                path="/menus/admin/user/:id"
                element={
                  <AdminRoute>
                    <UserEditScreen />
                  </AdminRoute>
                }
              ></Route>
              <Route
                path="/menus/admin/driver/:id"
                element={
                  <AdminRoute>
                    <DriverEditScreen />
                  </AdminRoute>
                }
              ></Route>

              <Route path="/menus" element={<HomeScreen />} />
            </Routes>
          </Container>
        </main>
        <footer>
          <div className="text-center">All rights reserved</div>
        </footer>
      </div>
    </BrowserRouter>
  );
}

export default App;
