import React, { useContext, useEffect, useReducer, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";
import axios from "axios";
import { Store } from "../Store";
import { getError } from "../utils";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import { Helmet } from "react-helmet-async";
import LoadingBox from "../components/LoadingBox";
import MessageBox from "../components/MessageBox";
import Button from "react-bootstrap/Button";

const reducer = (state, action) => {
  switch (action.type) {
    case "FETCH_REQUEST":
      return { ...state, loading: true };
    case "FETCH_SUCCESS":
      return { ...state, loading: false };
    case "FETCH_FAIL":
      return { ...state, loading: false, error: action.payload };
    case "UPDATE_REQUEST":
      return { ...state, loadingUpdate: true };
    case "UPDATE_SUCCESS":
      return { ...state, loadingUpdate: false };
    case "UPDATE_FAIL":
      return { ...state, loadingUpdate: false };
    case "UPLOAD_REQUEST":
      return { ...state, loadingUpload: true, errorUpload: "" };
    case "UPLOAD_SUCCESS":
      return {
        ...state,
        loadingUpload: false,
        errorUpload: "",
      };
    case "UPLOAD_FAIL":
      return { ...state, loadingUpload: false, errorUpload: action.payload };

    default:
      return state;
  }
};
export default function DriverEditScreen() {
  const navigate = useNavigate();
  const params = useParams(); // /restaurant/:id
  const { id: driverId } = params;

  const { state } = useContext(Store);
  const { userInfo } = state;
  const [{ loading, error, loadingUpdate }, dispatch] = useReducer(reducer, {
    loading: true,
    error: "",
  });

  const [name, setName] = useState("");
  const [phoneNumer, setPhoneNumer] = useState("");
  const [whatsappGroups, setWhatsappGroups] = useState([]);
  const [selectedWhatsappGroup, setSelectedWhatsappGroup] = useState("");

  useEffect(() => {
    const fetchWhatsappGroups = async () => {
      try {
        const response = await axios.get(
          "https://arkan.hajat.com.ly/getgroups/arkan"
        );
        setWhatsappGroups(response.data);
        dispatch({
          type: "FETCH_SUCCESS",
        });
      } catch (error) {
        console.error("There was a problem with the fetch operation:", error);
      }
    };
    const fetchData = async () => {
      try {
        dispatch({ type: "FETCH_REQUEST" });
        const { data } = await axios.get(`/api/users/driver/${driverId}`);
        setName(data.name);
        setPhoneNumer(data.phoneNumer);
        setSelectedWhatsappGroup(data.whatsappGroupID);
        dispatch({ type: "FETCH_SUCCESS" });
      } catch (err) {
        dispatch({
          type: "FETCH_FAIL",
          payload: getError(err),
        });
      }
    };
    fetchWhatsappGroups();
    fetchData();
  }, [driverId]);
  const submitHandler = async (e) => {
    e.preventDefault();
    try {
      dispatch({ type: "UPDATE_REQUEST" });
      await axios.put(
        `/api/users/driver/${driverId}`,
        {
          name,
          phoneNumer,
          whatsappGroupID: selectedWhatsappGroup,
        },
        {
          headers: { Authorization: `Bearer ${userInfo.token}` },
        }
      );
      dispatch({
        type: "UPDATE_SUCCESS",
      });
      toast.success("Driver updated successfully");
      navigate("/menus/admin/users");
    } catch (err) {
      toast.error(getError(err));
      dispatch({ type: "UPDATE_FAIL" });
    }
  };
  return (
    <Container className="small-container">
      <Helmet>
        <title>Create Driver</title>
      </Helmet>
      <h1>Create Driver</h1>

      {loading ? (
        <LoadingBox></LoadingBox>
      ) : error ? (
        <MessageBox variant="danger">{error}</MessageBox>
      ) : (
        <Form onSubmit={submitHandler}>
          <Form.Group className="mb-3" controlId="name">
            <Form.Label>Name</Form.Label>
            <Form.Control
              value={name}
              onChange={(e) => setName(e.target.value)}
              required
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="phoneNumer">
            <Form.Label>Phone Numer</Form.Label>
            <Form.Control
              value={phoneNumer}
              onChange={(e) => setPhoneNumer(e.target.value)}
              required
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="whatsappGroup">
            <Form.Label>Whatsapp Group ID</Form.Label>
            <Form.Select
              onChange={(e) => setSelectedWhatsappGroup(e.target.value)}
              value={selectedWhatsappGroup}
            >
              <option>Select a whatsappGroup</option>
              {whatsappGroups.map((whatsappGroup) => (
                <option
                  key={whatsappGroup.id}
                  value={whatsappGroup.id}
                  selected={whatsappGroup.id === selectedWhatsappGroup}
                >
                  {whatsappGroup.name}
                </option>
              ))}
            </Form.Select>
          </Form.Group>
          <div className="mb-3">
            <Button disabled={loadingUpdate} type="submit">
              Create
            </Button>
            {loadingUpdate && <LoadingBox></LoadingBox>}
          </div>
        </Form>
      )}
    </Container>
  );
}
