import React, { useEffect, useReducer } from 'react';
import { useNavigate, useLocation, NavLink } from 'react-router-dom';
import axios from 'axios';
import { getError } from '../utils';
import { Helmet } from 'react-helmet-async';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import LoadingBox from '../components/LoadingBox';
import MessageBox from '../components/MessageBox';
import Button from 'react-bootstrap/Button';
import Restaurant from '../components/Restaurant';

const reducer = (state, action) => {
  switch (action.type) {
    case 'FETCH_REQUEST':
      return { ...state, loading: true };
    case 'FETCH_SUCCESS':
      return {
        ...state,
        restaurants: action.payload.restaurants,
        page: action.payload.page,
        pages: action.payload.pages,
        countrestaurants: action.payload.countRestaurants,
        loading: false,
      };
    case 'FETCH_FAIL':
      return { ...state, loading: false, error: action.payload };

    default:
      return state;
  }
};

export default function SearchScreen2() {
  const navigate = useNavigate();
  const { search } = useLocation();
  const sp = new URLSearchParams(search); // /search?category=Shirts
  const query = sp.get('query') || 'all';
  const page = sp.get('page') || 1;

  const [{ loading, error, restaurants, pages, countrestaurants }, dispatch] =
    useReducer(reducer, {
      loading: true,
      error: '',
    });

  useEffect(() => {
    const fetchData = async () => {
      try {
        const { data } = await axios.get(
          `/api/restaurants/search?page=${page}&query=${query}`
        );
        dispatch({ type: 'FETCH_SUCCESS', payload: data });
      } catch (err) {
        dispatch({
          type: 'FETCH_FAIL',
          payload: getError(error),
        });
      }
    };
    fetchData();
  }, [error, page, query]);

  const getFilterUrl = (filter, skipPathname) => {
    const filterPage = filter.page || page;
    const filterQuery = filter.query || query;
    return `${
      skipPathname ? '' : '/menus/search2?'
    }&query=${filterQuery}&page=${filterPage}`;
  };
  return (
    <div>
      <Helmet>
        <title>Search restaurants</title>
      </Helmet>
      <Row>
        <Col md={9}>
          {loading ? (
            <LoadingBox></LoadingBox>
          ) : error ? (
            <MessageBox variant="danger">{error}</MessageBox>
          ) : (
            <>
              <Row className="justify-content-between mb-3">
                <Col md={6}>
                  <div>
                    {countrestaurants === 0 ? 'No' : countrestaurants} Results
                    {query !== 'all' && ' : ' + query}
                    {query !== 'all' ? (
                      <Button
                        variant="light"
                        onClick={() => navigate('/menus/search2')}
                      >
                        <i className="fas fa-times-circle"></i>
                      </Button>
                    ) : null}
                  </div>
                </Col>
              </Row>
              {restaurants.length === 0 && (
                <MessageBox>No Restaurant Found</MessageBox>
              )}

              <Row>
                {restaurants.map((restaurant) => (
                  <Col sm={6} lg={4} className="mb-3" key={restaurant._id}>
                    <Restaurant restaurant={restaurant} />
                  </Col>
                ))}
              </Row>

              <div>
                {[...Array(pages).keys()].map((x) => (
                  <NavLink
                    key={x + 1}
                    className="mx-1"
                    to={`/menus/search2?${getFilterUrl({ page: x + 1 }, true)}`}
                  >
                    <Button
                      className={Number(page) === x + 1 ? 'text-bold' : ''}
                      variant="light"
                    >
                      {x + 1}
                    </Button>
                  </NavLink>
                ))}
              </div>
            </>
          )}
        </Col>
      </Row>
    </div>
  );
}
