import axios from 'axios';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import { Helmet } from 'react-helmet-async';
import { useContext, useEffect, useState } from 'react';
import { Store } from '../Store';
import { toast } from 'react-toastify';
import { getError } from '../utils';

export default function MakeOrderScreen() {
  const navigate = useNavigate();
  const { search } = useLocation();
  const redirectInUrl = new URLSearchParams(search).get('redirect');
  const redirect = redirectInUrl ? redirectInUrl : '/';

  const [name, setName] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [address, setAddress] = useState('');
  const [selectedDriver, setSelectedDriver] = useState('');
  const [shippingPrice, setShippingPrice] = useState('');
  const [drivers, setDrivers] = useState([]);
  const { state, dispatch: ctxDispatch } = useContext(Store);
  const {
    userInfo,
    cart: { cartItems },
  } = state;
  const submitHandler = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post(
        '/api/orders',
        {
          cartItems,
          name,
          phoneNumber,
          address,
          selectedDriver,
          shippingPrice,
        },
        {
          headers: {
            authorization: `Bearer ${userInfo.token}`,
          },
        }
      );
      ctxDispatch({ type: 'CART_CLEAR' });
      toast.success('تم ارسال الطلب للمندوب بنجاح');
      localStorage.removeItem('cartItems');
      navigate('/menus');
    } catch (err) {
      toast.error(getError(err));
    }
  };
  const fetchDrivers = async () => {
    try {
      const response = await axios.get('/api/users/drivers');
      setDrivers(response.data);
    } catch (error) {
      console.error('There was a problem with the fetch operation:', error);
    }
  };

  useEffect(() => {
    fetchDrivers();
  }, []);
  return (
    <Container className="small-container">
      <Helmet>
        <title>Make Order</title>
      </Helmet>
      <h1 className="my-3">Make Order</h1>
      <Form onSubmit={submitHandler}>
        <Form.Group className="mb-3" controlId="name">
          <Form.Label>Cutomer Name</Form.Label>
          <Form.Control onChange={(e) => setName(e.target.value)} />
        </Form.Group>
        <Form.Group className="mb-3" controlId="phoneNumber">
          <Form.Label>Cutomer Phone Number</Form.Label>
          <Form.Control onChange={(e) => setPhoneNumber(e.target.value)} />
        </Form.Group>
        <Form.Group className="mb-3" controlId="address">
          <Form.Label>Address</Form.Label>
          <Form.Control onChange={(e) => setAddress(e.target.value)} />
        </Form.Group>
        <Form.Group className="mb-3" controlId="driver">
          <Form.Label>Driver</Form.Label>
          <Form.Select
            onChange={(e) => setSelectedDriver(e.target.value)}
            value={selectedDriver}
          >
            <option>Select a driver</option>
            {drivers.map((driver) => (
              <option key={driver._id} value={driver._id}>
                {driver.name}
              </option>
            ))}
          </Form.Select>
        </Form.Group>
        <Form.Group className="mb-3" controlId="shippingPrice">
          <Form.Label>Shipping Price</Form.Label>
          <Form.Control
            value={shippingPrice}
            onChange={(e) => setShippingPrice(e.target.value)}
          />
        </Form.Group>
        <div className="mb-3">
          <Button type="submit">Make Order</Button>
        </div>
      </Form>
    </Container>
  );
}
